@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);
@import url(https://fonts.googleapis.com/icon?family=Material+Icons);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  font-family: 'Open Sans', sans-serif;
}

.w-0.to-full{
  width: 100%;
  transition: width 2500ms ease-in-out;
}
.w-0.to-95{
  width: 95%;
  transition: width 2500ms ease-in-out;
}
.w-0.to-90{
  width: 90%;
  transition: width 2500ms ease-in-out;
}
.w-0.to-75{
  width: 75%;
  transition: width 2500ms ease-in-out;
}

button:focus, button:active{
  outline: none;
}

#left-panel{
  background: #f5f5f7;
  box-shadow: 0 0 .5rem .125rem rgba(0,0,0,.25);
  transition: width 300ms ease-in-out;
}

.border-b-gray-300{
  border-bottom-color: rgba(209, 213, 219,1);
}
.button-menu{
  background-color: white;
  color: black;
  transition: all 150ms ease-in-out;
}
.button-menu:first-child{
  border-radius: 0.25rem 0 0 0.25rem;
}
.button-menu:last-child{
  border-radius: 0 0.25rem 0.25rem 0;
}
.button-menu:hover{
  background-color: rgba(230,230,230,1);
  color: gray;
  transition: all 150ms ease-in-out;
}
.button-menu.button-selected{
  background-color: rgba(220,38,38,1);
  color: white;
  transition: all 150ms ease-in-out;
}
.button-menu.button-selected:hover{
  background-color: rgba(160,0,0,1);
  color: white;
  transition: all 150ms ease-in-out;
}

/* FADES ANIMATION CLASS */
.fades-enter,.fades-exit{
  opacity: 0;
  height: 0;
  transform: translateY(-10%);
  transition: opacity 300ms ease-in-out,transform 300ms ease-in-out,height 300ms ease-in-out;
}
.fades-enter.fades-enter-active{
  opacity: 1;
  height: auto;
  transform: translateY(0%);
  transition: opacity 300ms ease-in-out,transform 300ms ease-in-out,height 300ms ease-in-out;
}


@media (min-width: 768px){
  .md\:w\-1\/3\-sm{
    width: 30%;
  }
}
